.Education_Page {
  width: 85%;
  margin: auto;
  /* padding-top: 10vh; */
  /* height: 100vh; */
  background-color: #272727;
  color: white;
}

h1{
  color: #ffffff;
  text-shadow:
  0 0 4px #fff,
  0 0 11px rgb(225, 0, 255),
  0 0 19px rgb(225, 0, 255),
  0 0 40px rgb(225, 0, 255),
  0 0 80px rgb(225, 0, 255);
}


.Education_cards_component{
  /* border: 2px solid #a221b0; */
    display: grid;
    grid-template-columns: repeat(1,1fr);
    gap: 20px;
}
.edu_card {
  text-align: left;
padding: 25px 25px;
  background: #03073660;
  border: white;

}
.edu_card_p{
display: flex;

}
