display: flex;
justify-content: space-between;

}
.Project_card {
  /* width: 100%; */

  border: 1px solid rgba(255, 255, 255, 0.212);
  padding: 0px 20px;
  transition: transform 0.3s ease;
  z-index: 0;
  display: flex;
  flex-direction: column;

  align-items: center;
}


.Project_card_img_container{
 display: flex;
  align-items: center;
}
.Project_card_img_container > img {
  width: 100%;
  margin: auto;
  /* border: 2px solid red; */
}

.Project_card:hover {
  background-color: rgba(61, 0, 121, 0.358);
;
  transform: scale(1.05);
  box-shadow: 0 0 100px rgba(183, 0, 180, 0.54);
}
.Project_card_text {
  /* border: 2px solid white; */
display: flex;
padding: 0px 20px 0px;
flex-direction: column;
  text-align: justify;
  margin: 0px 20px;
  margin: auto;
  align-items: center;
  /* background-color: #565f92; */
}

.Project_card_text > h2 {
  font-weight: 100;
}
.Project_card_text > h2 > span {
  font-size: small;
  font-weight: 600;
}
.Project_card_text > button {
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  background-color: rgb(234, 0, 255);
  color: white;
}
.Project_card_text > button:hover {
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
  color: rgb(195, 0, 209);
  border: 2px solid rgb(147, 64, 140);
}
.Project_card_text_desc{
  /* border: 2px solid red; */
}
.Project_card_text_hidden {
  opacity: 1;
  height: auto;
  overflow: hidden;
  transition: opacity 0.3s ease-out, height 0.3s ease-out;
  opacity: 0;
  height: 0;
}
.Project_card_text_hidden2 {
  opacity: 1;
  height: auto;
  overflow: hidden;
  transition: opacity 0.3s ease-out, height 0.3s ease-out;
  opacity: 0;
  height: 0;
}
