.project_deployed_link {
  animation: flicker 1.5s infinite alternate;
  color: #ffffff;

cursor:pointer;
}

a{
  text-decoration: none;
}
@keyframes flicker {
    
  0%,  22%,  53%, 57%, 100% {

      text-shadow:
      0 0 4px #fff,
      0 0 11px rgb(255, 0, 128),
      0 0 19px rgb(255, 0, 140),
      0 0 40px rgb(222, 255, 57),
      0 0 80px rgb(0, 255, 242)
      
  
  }
  
  20%, 24%, 55% {        
      text-shadow: none;
  }    
}
@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}