 .resumeBtn{
    width: 150px;
    height: 45px;
    font-size: 20px;
    border-radius: 15px;
    background-color:#4F3679;
    border: 2px solid #c995fb ;
    color: white;
    outline: none;
 }
 .resumeBtn:hover{
    width: 150px;
    height: 45px;
    font-size: 20px;
    border-radius: 15px;
    background: linear-gradient(135deg, #3e2743, #25668c);
    border: 2px solid #781bcf ;
    color: white;
    outline: none;
 }





.About>h1 {
  animation: flicker 1.5s infinite alternate;
  color: #fff;

}


/* Flickering animation */
@keyframes flicker {
  0%, 22%, 53%, 57%, 100% {
    text-shadow:
      0 0 4px #fff,
      0 0 15px rgb(225, 0, 255),
      0 0 25px rgb(225, 0, 255),
      0 0 35px rgb(225, 0, 255),
      0 0 50px rgb(225, 0, 255);
  }

  20%, 24%, 55% {        
    text-shadow: none;
  }    
}





@media (max-width: 800px) {
