.content{
  height: 100vh;
}

.Navbar {
  color: white;
  height: 10vh;
  width: 90%;
  margin: auto;
  border-bottom: 1px solid rgba(228, 228, 228, 0.614);
  display: flex;
  padding: 0px 50px;
  /* padding-bottom: 10px; */
  justify-content:space-between;
  align-items: center;
/* 
  border: 2px solid red; */
  box-sizing: border-box;
}
.Navbar:hover{
cursor: pointer;
}
.FixedNavbar {
  
  position: fixed;
  top: 0;
  left: 0;
  background-color: #fff; /* Update with your desired background color */
  /* Add other styles as needed */
  /* border: 2px solid red; */
  z-index: 2;
}
.Navbar_links {
  text-decoration: none;
  color: white;
  font-weight: 400;
  margin: 20px;
  
}

.profile_banner {
 /* border: 5px solid red; */
    color: white;
    display: flex;
    width: 70%;
    margin: auto  ;
    line-height: 25px;
    align-items: center;
    justify-content: space-evenly;
   
    padding:150px 50px 50px 50px;
    height: 80vh;

  }
  
  .profile_banner_image {
    width: 30%;
    border: 2px dashed white;
   
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center; 
    padding: 25px;
  }
  
  .profile_banner_image > img {
    width: 100%;
    border: 1px solid white;
    border-radius: 100%;
  }
  


  .profile_banner_text{

    width: 50%;
    font-family: 'Mulish', sans-serif;
text-align: left;
font-weight:400;
}
.profile_banner_text>h1{
    font-size: 50px;
line-height: 50px;
font-family: 'Ysabeau', sans-serif; 
}


.Hamburger{
  visibility: hidden;
}

.resumebtn{
  width: 100px;
  height: 45px;
  font-size: 20px;
  border-radius: 15px;
  background-color:#4F3679;
  border: 2px solid #c995fb ;
  color: white;
  outline: none;
}
.resumebtn:hover{

  height: 45px;
  font-size: 20px;
  border-radius: 15px;
  background: linear-gradient(135deg, #1c2e2e, #a108b5);
  border: 2px solid #781bcf ;
  color: white;
  outline: none;
}







