}


.togglebtn{
  visibility: hidden;
}
.neonText {
  animation: flicker 1.5s infinite alternate;
  color: #fff;

}
.navbarlinkscolumn {
  margin-top: 10vh;
  padding-top: 20px;
  width: 80%;
  position: fixed;
  top: 0;
  
  background-color: #fff; 
  background-color: #0f2f4f;
  display: flex;
  height: 100vh;
  top: 0; /* Stick the navbar links to the top of the parent container */
  left: 0; /* Span the full width of the parent container */
  right: 0; /* Span the full width of the parent container */
  flex-direction: column;
  align-items: center;
  overflow-y: auto; /* Enable vertical scrolling if content overflows */
}
.hide{
 display: none;
}
.column{
  visibility: hidden;
border: 5px solid red;
display: flex;
flex-direction: column;
}



