/* Flickering animation */
@keyframes flicker {
    
  0%,  22%,  53%, 57%, 100% {

      text-shadow:
      0 0 4px #fff,
      0 0 11px rgb(225, 0, 255),
      0 0 19px rgb(225, 0, 255),
      0 0 40px rgb(225, 0, 255),
      0 0 80px rgb(225, 0, 255)
      
  
  }
  
  20%, 24%, 55% {        
      text-shadow: none;
  }    
}


@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



