.SkillPage {
  width: 100%;
  margin: auto;
  height: 100vh;
  padding-top: 50px;
  
  padding-bottom: 50px;


}

.Skillcards {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  margin: auto;
  display: flex; /* Use flexbox for horizontal alignment */
  justify-content: center;
  flex-wrap: wrap; /* Wrap the images to a new line if they exceed the container width */
}

.Skillcards > div {
  flex: 0 0 33%; /* Each div takes 33% width to show three images in a row */
  padding: 10px; /* Add some padding between images */
  box-sizing: border-box;
}

.Skillcards > div img {
  background: #55367d;
  background: linear-gradient(135deg, #55367d, #9accff);
  padding: 20px;
  border-radius: 100%;
  height: 200px; 
  width: 200px;
  /* Set a fixed height for consistent size */
  object-fit: cover;
  object-position: center;
}
